@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
body {
  background-color: rgb(68, 226, 226);
  font-family: "Shadows Into Light", cursive;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar-bar {
  height: 20%;
  width: 100%;
  background-color: rgb(16, 21, 36);
}
ul {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
}
li {
  padding: 2rem;
}
a {
  color: #fff;
  margin-left: 1rem;
  margin-right: 1rem;
  text-decoration: none;
}
li:hover {
  background-color: rgb(68, 226, 226);
}

.header-title {
  text-align: center;
  width: 100%;
  height: 20%;
  margin: 2rem 0;
}

.cards-group {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}

.cards {
  width: 350px;
  height: auto;
  background-color: rgb(12, 27, 56);
  border-radius: 5px;
  padding: 1rem;
}
h3 {
  color: #fff;
  margin-bottom: 1rem;
}
.image {
  width: 99%;
  height: 260px;
  background-color: black;
  position: relative;
}
img {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center;
  border-radius: 15px;
}

p {
  opacity: 0;
  position: absolute;
  bottom: 0%;
  left: 5%;
  color: #fff;
}
.image:hover p {
  opacity: 1;
  bottom: 20%;
  transition: all 0.5s ease-in-out;
}

